import request from '@/utils/request'

// 添加主体下的产品或编辑产品
export function Productaddoredit(data) {
    return request({
        url: '/product/addoredit',
        method: 'post',
        data: data
    })
}

// 获取主体下的全部产品数据
export function Productlist(data) {
    return request({
        url: '/product/list',
        method: 'post',
        data: data
    })
}

// 管理产品
export function Productmanager(data) {
    return request({
        url: '/product/manager',
        method: 'post',
        data: data
    })
}

// 添加产品与设备从机的绑定
export function Productobjbindaddoredit(data) {
    return request({
        url: '/product/obj/bind/addoredit',
        method: 'post',
        data: data
    })
}

// 获取产品下的全部设备从机绑定数据
export function Productobjbinddata(data) {
    return request({
        url: '/product/obj/bind/data',
        method: 'post',
        data: data
    })
}

// 管理产品下的设备从机绑定
export function Productobjbindmanager(data) {
    return request({
        url: '/product/obj/bind/manager',
        method: 'post',
        data: data
    })
}