<template>
  <div>
    <div class="module filter">
      <div class="l">
        <el-input placeholder="请输入内容" v-model="form.keyword">
          <template slot="prepend">搜索主体</template>
        </el-input>
      </div>
      <div class="l">
        <el-button @click="getList()">搜索</el-button>
      </div>

      <div class="r">
        <router-link to="/iot/product">
          <el-button>返回</el-button>
        </router-link>
      </div>
    </div>

    <div class="module">{{info}}</div>

    <div class="module">
      <div class="l">
        <router-link :to=" {path:'/iot/product/edit', query:{product_id: this.product_id, principal_id: this.principal_id}}">
          <el-button>新增关联从机</el-button>
        </router-link>
      </div>
    </div>

    <div class="module">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="id" label="系统ID" width="100" align="center"> </el-table-column>

        <el-table-column prop="product_id" label="产品ID">
        </el-table-column>

        <el-table-column prop="equipmentdata.name" label="绑定的数据从机">
        </el-table-column>

        <el-table-column prop="quipment_type" label="绑定类型" width="250">
          <template slot-scope="scope">
            <span v-show="scope.row.quipment_type == 1">从机</span>
            <span v-show="scope.row.quipment_type == 2">摄像头</span>
          </template>
        </el-table-column>

        <el-table-column label="更多" fixed="right" width="125" align="center">
          <template slot-scope="scope">
            <el-dropdown>
              <el-button type="primary">
                管理<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <!-- <el-dropdown-item @click.native="updatedia(scope.row)">修改</el-dropdown-item> -->
                <el-dropdown-item @click.native="dele(scope.row)">删除</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog title="修改设备类型" :visible.sync="dialogVisible" width="30%" :show-close='false'
        :close-on-click-modal='false'>
        <div class="mtitle">设备类型</div>
        <el-select v-model="form.quipment_type" placeholder="请选择设备类型">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click.native="updateList">确 定</el-button>
        </span>
      </el-dialog>
    </div>

    <div class="module">
      <Pagination @pagination="getList()" :page.sync="form.querypage" :pageCount.sync="totalPage" :total.sync="total"
        v-if="total > 0" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import {
  Productobjbinddata,
  Productaddoredit,
  Productobjbindmanager
} from "@/api/product";
import {
  Queryproductdata
} from "@/api/data";
import Pagination from "@/components/Pagination.vue";
export default {
  Productaddoredit,
  name: "productAll",
  data() {
    return {
      form: {
        keyword: "",
        querypage: 1,
      },
      tableData: [], // 列表
      total: 0, // 总条目
      totalPage: 0, // 总页数
      dialogVisible: false,
      info: null,
      updateTable: [],
      options: [
        {
          value: "1",
          label: "从机",
        },
        {
          value: "2",
          label: "摄像头",
        },
      ],
    };
  },
  components: {
    Pagination,
  },
  methods: {
    getInfo(){
      this.$http(Queryproductdata({
        id: this.$route.query.product_id
      }), (res) => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
          this.info = res.data
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    getList() {
      if (this.product_id != "") {
        this.form.product_id = this.product_id;
      }
      this.$http(Productobjbinddata(this.form), (res) => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
          this.total = res.data.total;
          this.form.querypage = res.data.currentPage;
          this.totalPage = res.data.totalPages;
          this.tableData = res.data;
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    updatedia(item) {
      this.dialogVisible = true;
      this.updateTable = item;
    },
    dele(item) {
      this.$confirm(
        "",
        "这是一段内容确定删除吗？",
        () => {
          //删除接口
          this.$http(
            Productobjbindmanager({
              id: item.id,
              set: 1,
            }),
            (res) => {
              if (res.code == 200) {
                this.$message({
                  message: res.msg,
                  type: "success",
                });
                this.getList();
              } else {
                this.$message({
                  message: res.msg,
                  type: "error",
                  duration: 5 * 1000,
                });
              }
            }
          );
        },
        () => {
          this.$message("已取消");
        }
      );
    },
    // detail(item) {
    //   this.updateTable = item;
    // },
    updateList() {

    },
  },
  created() {
    this.product_id = this.$route.query.product_id;
    this.principal_id = this.$route.query.principal_id;
    this.getList();
    this.getInfo();
  },
};
</script>
<style lang="scss" scoped>
</style>